import { addDocument, deleteDocument } from "api/collections";
import { Command } from "utils/classes/Command";
import { Document } from "utils/interfaces";

export class DeleteMultipleDocuments extends Command {
  collectionId: string;
  data: Document[];
  callback: () => void;

  constructor(collectionId: string, data: Document[], callback: () => void) {
    super();
    this.collectionId = collectionId;
    this.data = data;
    this.callback = callback;
  }

  async undo() {
    await Promise.all(
      this.data.map((x) => addDocument(this.collectionId, { ...x, docId: x.docId }))
    );
    this.callback();
  }
  async execute() {
    await Promise.all(this.data.map((x) => deleteDocument(this.collectionId, x.docId)));
    this.callback();
  }
}
