import { addDocument, deleteDocument } from "api/collections";
import { Command } from "utils/classes/Command";

export class DeleteDocument extends Command {
  collectionId: string;
  data: any;
  docId = "";
  callback: () => void;

  constructor(collectionId: string, docId: string, callback: () => void) {
    super();
    this.collectionId = collectionId;
    this.docId = docId;
    this.callback = callback;
  }

  async undo() {
    await addDocument(this.collectionId, { ...this.data, docId: this.docId });
    this.callback();
  }
  async execute() {
    await deleteDocument(this.collectionId, this.docId);
    this.callback();
  }
}
