import { Command } from "utils/classes/Command";

export class CommandsController {
  history: Command[] = [];
  lastCommand: number = -1;
  execute(command: Command) {
    this.history = this.history.slice(0, this.lastCommand + 1);
    this.history.push(command);
    this.lastCommand++;
    this.history[this.lastCommand].execute();
  }
  undo() {
    if (this.lastCommand >= 0) {
      this.history[this.lastCommand].undo();
      this.lastCommand--;
    }
  }

  redo() {
    if (this.lastCommand < this.history.length - 1) {
      this.lastCommand++;
      this.history[this.lastCommand].execute();
    }
  }
}
