import { addDocument, deleteDocument } from "api/collections";
import { Command } from "utils/classes/Command";

export class AddDocument extends Command {
  collectionId: string;
  data: any;
  docId = "";
  callback: () => void;

  constructor(collectionId: string, data: any, callback: () => void) {
    super();
    this.collectionId = collectionId;
    this.data = data;
    this.callback = callback;
  }

  async execute() {
    const res = await addDocument(this.collectionId, this.data);
    if (!res.error) {
      this.docId = res.docId;
    }
    this.callback();
  }
  async undo() {
    if (!this.docId) return;
    await deleteDocument(this.collectionId, this.docId);
    this.callback();
  }
}
